import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { Input, Text } from 'kebabfinance-uikit'
import { useUserDeadline } from 'state/user/hooks'
import QuestionHelper from '../QuestionHelper'
import TranslatedText from '../TranslatedText'

const StyledTransactionDeadlineSetting = styled.div``
const Label = styled.div`
  align-items: center;
  display: flex;
  margin-bottom: 8px;
`
const Field = styled.div`
  align-items: center;
  display: inline-flex;

  & > ${Input} {
    max-width: 100px;
  }
`
const StyledInput = styled(Input)`
  height: 36px;
  border-radius: 6px;
  background-color: ${({ theme }) => theme.colors.currencySelectBackground};
  color: ${({ theme }) => theme.colors.footer};
  font-weight: 600;
  border: none;
  box-shadow: none !important;

  ::placeholder {
    color: ${({ theme }) => theme.colors.footer};
  }

  :focus {
    box-shadow: none !important;
  }
`

const TransactionDeadlineSetting = () => {
  const [deadline, setDeadline] = useUserDeadline()
  const [value, setValue] = useState(deadline / 60) // deadline in minutes
  const [error, setError] = useState<string | null>(null)

  const handleChange = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const { value: inputValue } = evt.target
    setValue(parseInt(inputValue, 10))
  }

  // Updates local storage if value is valid
  useEffect(() => {
    try {
      const rawValue = value * 60
      if (!Number.isNaN(rawValue) && rawValue > 0) {
        setDeadline(rawValue)
        setError(null)
      } else {
        setError('Enter a valid deadline')
      }
    } catch {
      setError('Enter a valid deadline')
    }
  }, [value, setError, setDeadline])

  return (
    <StyledTransactionDeadlineSetting>
      <Label>
        <Text bold>
          <TranslatedText translationId={90}>Transaction deadline</TranslatedText>
        </Text>
        <QuestionHelper text="Your transaction will revert if it is pending for more than this long." />
      </Label>
      <Field>
        <StyledInput type="number" step="1" min="1" value={value} onChange={handleChange} />
        <Text fontSize="14px" ml="12px" bold>
          minutes
        </Text>
      </Field>
      {error && (
        <Text mt="8px" color="failure">
          {error}
        </Text>
      )}
    </StyledTransactionDeadlineSetting>
  )
}

export default TransactionDeadlineSetting
